                v-if="filters.order_by_asc">
              Убыванию цены
            </button>
            <button
                class="app-white-btn"
                v-else
                @click="()=>switchPriceOrder()">
              Убыванию цены
            </button>
          </div>
          <div>
            <button
                class="app-blue-btn"
                v-if="!filters.order_by_asc">
              Возрастанию цены
            </button>
            <button
