            <img src="/icons/close_circled.svg"/>
          </button>
          <button
              class="app-white-btn"
              v-else
              @click="()=>changeStyle('hai_tek')">
            Хай-тек
          </button>
        </div>

      </div>

      <div class="heading-label">
        ЦВЕТ
      </div>
      <div class="flex">
        <div>
          <button
              class="app-blue-btn"
              v-if="filters.color.red"
              @click="()=>changeColor('red')">
            Красный &nbsp;
            <img src="/icons/close_circled.svg"/>
          </button>
          <button
              class="app-white-btn"
              v-else
              @click="()=>changeColor('red')">
            Красный
          </button>
        </div>
        <div>
          <button
              class="app-blue-btn"
              v-if="filters.color.yellow"
              @click="()=>changeColor('yellow')">
            Желтый &nbsp;
            <img src="/icons/close_circled.svg"/>
          </button>
          <button
              class="app-white-btn"
              v-else
              @click="()=>changeColor('yellow')">
            Желтый
          </button>
        </div>
        <div>
          <button
              class="app-blue-btn"
              v-if="filters.color.grey"
              @click="()=>changeColor('grey')">
            Серый &nbsp;
            <img src="/icons/close_circled.svg"/>
          </button>
          <button
              class="app-white-btn"
              v-else
              @click="()=>changeColor('grey')">
            Серый
          </button>
        </div>

        <div>
          <button
              class="app-blue-btn"
              v-if="filters.color.black"
              @click="()=>changeColor('black')">
            Черный &nbsp;
            <img src="/icons/close_circled.svg"/>
          </button>
          <button
              class="app-white-btn"
              v-else
              @click="()=>changeColor('black')">
            Черный
          </button>
        </div>
        <div>
          <button
              class="app-blue-btn"
              v-if="filters.color.blue"
              @click="()=>changeColor('blue')">
            Синий &nbsp;
            <img src="/icons/close_circled.svg"/>
          </button>
          <button
              class="app-white-btn"
              v-else
              @click="()=>changeColor('blue')">
            Синий
          </button>
        </div>
        <div>
          <button
              class="app-blue-btn"
              v-if="filters.color.turquoise"
              @click="()=>changeColor('turquoise')">
            Бирюзовый &nbsp;
            <img src="/icons/close_circled.svg"/>
          </button>
          <button
              class="app-white-btn"
              v-else
              @click="()=>changeColor('turquoise')">
            Бирюзовый
          </button>
        </div>
      </div>

      <div>
        <div class="heading-label">
          ЦЕНА, РУБ
        </div>
        <RangeInputElement
            :min="1990" :max="23459"
            :min-value="filters.start_price" :max-value="filters.end_price"
            :changed="priceRangeChanged"/>
      </div>
      <div>
        <div class="heading-label">
          ШИРИНА, СМ
        </div>
        <RangeInputElement
            :min="35" :max="3500"
            :min-value="filters.start_width" :max-value="filters.end_width"
            :changed="widthRangeChanged"/>
      </div>
      <div>
        <div class="heading-label">
          ГЛУБИНА, СМ
        </div>
        <RangeInputElement
            :min="45" :max="1500"
            :min-value="filters.start_deep" :max-value="filters.end_deep"
            :changed="deepRangeChanged"/>
      </div>

    </div>


    <BottomFixedPanelElement height="70">
      <button class="app-blue-btn apply-btn" @click="applyChanges">
        Применить
      </button>
    </BottomFixedPanelElement>

  </div>
</template>

<script setup>
import {reactive, defineProps} from "vue";
import store from "@/store";
import router from "@/router";
import RangeInputElement from "@/elements/Inputs/RangeInputElement"
import BottomFixedPanelElement from "@/elements/Panels/BottomFixedPanelElement"

console.log('init filters component')

const props = defineProps(['section'])

const filters = reactive({})
Object.assign(filters, store.state.search_filters)

console.log(store.state.search_filters)
console.log(filters)

const switchPriceOrder = () => {
  filters.order_by_asc = !filters.order_by_asc
}


const changeStyle = (name) => {
  filters.style[name] = !filters.style[name]
}

const changeColor = (name) => {
  filters.color[name] = !filters.color[name]
}

const priceRangeChanged = (vals) => {
  filters.start_price = vals[0]
  filters.end_price = vals[1]
}

const widthRangeChanged = (vals) => {
  filters.start_width = vals[0]
  filters.end_width = vals[1]
}

const deepRangeChanged = (vals) => {
  filters.start_deep = vals[0]
  filters.end_deep = vals[1]
}


const applyChanges = () => {
  store.commit('updateFilters', filters)

  router.push('/catalog_section/'+props.section)
}


</script>

<style src="../../assets/styles/buttons.css"></style>
