import store from "@/store";

export const isProductInFavourites = (productId) => {

    let contains = false
    for (let i = 0; i < store.state.favourite.length; i++) {
        if (store.state.favourite[i].id === productId) {
            contains = true
            break
        }
    }

    return contains
}


export const GetFavouriteUncategorizedAndCategory = (category) => {
    return store.state.favourite
        .filter(x => x.userCategory === category || x.userCategory === '')
}

