<template>
  <button @click="props.radioClick" :class="(condition ? classTrue : classFalse) + ' standart-size-btn flex'">
    <div class="middle-element">
      {{ props.text }}
    </div>
    <div v-if="props.iconUrl!==null&&props.iconUrl!==undefined"
         class="right-text middle-element">
      <img :src="props.iconUrl" />
    </div>
  </button>
</template>

<script setup>
import {ref, defineProps} from "vue";

const props = defineProps(['classTrue', 'classFalse',
  'condition', 'text', 'iconUrl', 'radioClick'])



const condition = ref(props.condition)
const classTrue = ref(props.classTrue)
const classFalse = ref(props.classFalse)


if(props.classFalse === undefined || props.classFalse === null){
  classFalse.value = 'defaultFalse'
}

if(props.classTrue === undefined || props.classTrue === null){
  classTrue.value = 'defaultTrue'
}

if(props.condition === undefined || props.condition === null){
  classTrue.value = false
}

</script>

<style src="../../assets/styles/all.css"></style>
<style scoped>

.middle-element{
  margin-top: auto;
  margin-bottom: auto;
}

.defaultTrue {
  margin-top: 10px;
  margin-bottom: 10px;

  background-color: white;
  border: none;
  box-shadow: 0px 0px 4px 2px rgba(158, 173, 196, 1);
}

.defaultFalse {
  margin-top: 10px;
  margin-bottom: 10px;

  background-color: white;
  border: none;
  box-shadow: 0px 0px 4px 2px rgba(34, 60, 80, 0.2);

}

</style>