<template>
  <div :style="'--width: '+width+'px;--height: '+height+'px;'">
    <div style="height: 0;">
      <div class="go-back-btn on-img-icon" @click="goBack">

        <img class="icon-img" src="/icons/left-arrow.svg"/>

      </div>
      <div class="media-btn on-img-icon">
        <img class="icon-img" src="/icons/media.svg"/>
      </div>
      <div
          v-if="product.liked"
          class="like-btn on-img-icon" @click="dislike">
        <img class="icon-img" src="/icons/heart_filled.svg"/>
      </div>
      <div
          v-else
          class="like-btn on-img-icon" @click="like">
        <img class="icon-img" src="/icons/heart.svg"/>
      </div>

      <div class="ar-btn">
        <div>
          <img src="/icons/ar.svg"/>
          Посмотреть в AR
        </div>
      </div>
    </div>
    <CarouselElement
        @click="clickImg"
        :width="width"
        :height="230"
        :img-urls="props.product.imgUrls"/>
  </div>
  <div class="description-container">


    <div class="flex">
      <div class="price">
        {{ product.price }} ₽
      </div>
      <div class="old-price">
        {{ product.oldPrice }} ₽
      </div>
      <div class="discount">
        {{ Math.round(((product.price - product.oldPrice) / product.oldPrice) * 100) }}%
      </div>
    </div>

    <div class="product-category">
      {{ product.itemType }}
    </div>

    <div class="product-name">
      {{ product.name }}
    </div>

    <div class="header-text">
      ЦВЕТ
    </div>
    <div class="colors">
      <div v-for="(color, i) in product.availableColors" :key="i" style="margin-right: 10px;">
        <div
            v-if="product.color === color.name"
            class="app-blue-btn lr-paddings">
          {{ color.text }}
        </div>
        <div
            v-else
            @click="()=> changeColor(color)"
            class="app-white-btn lr-paddings">
          {{ color.text }}
        </div>
      </div>
    </div>

    <div class="header-text" style="margin-bottom: 5px;">
      ОПИСАНИЕ
    </div>
    <div class="description">
      {{ product.description }}
    </div>

    <div class="params-grid">
      <div class="header-text">
        Ширина, см
      </div>
      <div>
        {{ product.width }}
      </div>
      <div class="header-text">
        Глубина, см
      </div>
      <div>
        {{ product.deep }}
      </div>
      <div class="header-text">
        Высота, см
      </div>
      <div>
        {{ product.height }}
      </div>
      <div class="header-text">
        Стиль
      </div>
      <div>
        {{ product.style }}
      </div>
    </div>

    <div class="header-text" style="margin-top: 10px;margin-bottom: 5px;">
      ПОХОЖИЕ ТОВАРЫ
    </div>

    <div>
      <ProductsCarouselElement :products="products"/>
    </div>


  </div>

  <BottomFixedPanelElement :height="70">
    <button
        v-if="product.addingCount===0"
        @click="addProduct"
        class="app-blue-btn to-basket-btn">
      <img src="/icons/basket.svg" /> &nbsp;
      В корзину за {{product.price}} ₽
    </button>

    <div v-else class="adding-products-container">
      <div class="adding-products">
        <div @click="removeProduct" class="control-icon">
          <img src="/icons/minus.svg"/>
        </div>
        <div class="counter">
          {{ product.addingCount }}
        </div>
        <div @click="addProduct" class="control-icon">
          <img v-if="product.addingCount !== product.countAvailable" src="/icons/plus.svg"/>
          <img v-else src="/icons/plus_disabled.svg"/>
        </div>
      </div>

      <button @click="()=>router.push('/basket')" class="app-blue-btn basket-link-btn">
        К корзине
      </button>
    </div>
  </BottomFixedPanelElement>

</template>

<script setup>
import store from "@/store";
import router from "@/router";
import {defineProps, ref, reactive} from "vue";
import CarouselElement from '@/elements/Carousels/CarouselElement'
import ProductsCarouselElement from "@/elements/Carousels/ProductsCarouselElement";
import BottomFixedPanelElement from "@/elements/Panels/BottomFixedPanelElement"
import productService from "@/services/productService";

const props = defineProps(['product'])

const product = reactive(props.product)

console.log("prod comp" ,props.product)

const clickImg = () => {
  console.log('clclclc')
  router.push('/productImages/'+props.product.id)
}


const imgs = reactive([
  'https://catherineasquithgallery.com/uploads/posts/2021-02/1612503153_91-p-koshka-na-serom-fone-134.jpg',
  'https://www.wallpaperflare.com/static/341/974/440/kitten-grass-face-furry-wallpaper.jpg',
  'https://catherineasquithgallery.com/uploads/posts/2021-02/1612503153_91-p-koshka-na-serom-fone-134.jpg'
])




const addProduct = () => {
  if (product.addingCount !== product.countAvailable) {
    product.addingCount += 1
    productService.AddToBasket(product.id)
    //store.dispatch('updateBasket', product)
  }
}

const removeProduct = () => {
  product.addingCount -= 1
  //store.dispatch('updateBasket', product)
}

const products = [
  {
    price: 100,
    oldPrice: 120,
    id: 1,
    name: 'sas',
    description: 'ksqfh owijb o jwefoibj ijw joei jfoi jofj oefi joi je ojj',
    imgUrls: imgs
  },
  {
    price: 100,
    oldPrice: 120,
    id: 2,
    name: 'sas',
    description: 'ksqfh owijb o jwefoibj ijw joei jfoi jofj oefi joi je ojj',
    imgUrls: imgs
  },
  {
    price: 100,
    oldPrice: 120,
    id: 3,
    name: 'sas',
    description: 'ksqfh owijb o jwefoibj ijw joei jfoi jofj oefi joi je ojj',
    imgUrls: imgs
  },
  {
    price: 100,
    oldPrice: 120,
    id: 4,
    name: 'sas',
    description: 'ksqfh owijb o jwefoibj ijw joei jfoi jofj oefi joi je ojj',
    imgUrls: imgs
  },
  {
    price: 100,
    oldPrice: 120,
    id: 5,
    name: 'sas',
    description: 'ksqfh owijb o jwefoibj ijw joei jfoi jofj oefi joi je ojj',
    imgUrls: imgs
  },
]




const changeColor = (color) => {
  product.color = color.name
}

const goBack = () => {
  if (store.state.previousPageData.url !== '') {
    router.push(store.state.previousPageData.url)
  } else {
    router.push('/catalog')
  }
}

const like = () => {
  product.liked = true
  console.log('product.liked = true')
}

const dislike = () => {
  product.liked = false
  console.log('product.liked = !true')
}

const width = ref(window.screen.width)
const height = ref(230)

</script>

