/*}*/
/*[list]::-webkit-calendar-picker-indicator { display: none; }*/
/*input::-webkit-calendar-picker-indicator {*/
/*  display: none;*/
/*  -webkit-appearance: none;*/
/*}*/

input::-webkit-calendar-picker-indicator {
  opacity: 0;
}





.input-label-error-text {
  color: #FE4747;
  text-align: right;
}

.input-label {
  display: flex;
}

input[type=password]::-ms-reveal,
input[type=password]::-ms-clear {
  display: none;
}

.right-icon-input {
  border: 1px solid #C4C4C4;
  padding: 10px;
  display: flex;
}

#input {
  height: 40px;
  margin: 0;
  padding: 0 0 0 10px;
}

img.password {
  margin-top: 12px;
  margin-right: 5px;
}

img:hover {
  cursor: pointer;
}

div {
  text-align: left;
}

label {
  height: 13px;
  top: 3.5px;
  font-family: Open Sans, serif;
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 13px;
  letter-spacing: 0em;
  text-align: left;
  color: #757577;
}

input {
  font-family: 'Open Sans', serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  border: none;
  width: 100%;
  letter-spacing: 0em;
  text-align: left;

}

input:focus {
  border: none;
  outline: none;
}


</style>