<template>
  <div>
    <div class="header">
      <div class="img-container">
        <router-link to="/favourites/categories">
          <img src="/icons/close.svg" />
        </router-link>
      </div>
      <div class="heading">
        Добавить в категорию
      </div>
    </div>
    <div v-for="(product, i) in products" :key="i">
      <ProductCheckBoxElement
          :value="(product.userCategory === props.category)"
          :change="change"
          :product="product"/>
    </div>

    <BottomFixedPanelElement :height="70">
      <button class="app-blue-btn apply-btn" @click="addToCategory">
        Добавить выбранное в категорию
      </button>
    </BottomFixedPanelElement>
  </div>
</template>


<script setup>
import {defineProps, reactive} from "vue";
import ProductCheckBoxElement from "@/elements/Cards/ProductCheckBoxElement";
import BottomFixedPanelElement from "@/elements/Panels/BottomFixedPanelElement";
import store from "@/store";
import {GetFavouriteUncategorizedAndCategory} from "@/actions/storeActions";
import router from "@/router";


const props = defineProps(['category'])

const uncategorizedPlusCategoryProducts = GetFavouriteUncategorizedAndCategory(props.category)

const products = reactive(uncategorizedPlusCategoryProducts)

const selectedProductIds = reactive([])


console.log(props.category === products[0].userCategory)
console.log(props.category)
console.log(store.state.favourite)
console.log(uncategorizedPlusCategoryProducts)


const addToCategory = () => {
  store.commit('updateFavouriteCategories', [selectedProductIds, props.category])

  console.log(GetFavouriteUncategorizedAndCategory(props.category))

  router.push('/favourites/categories')
}


uncategorizedPlusCategoryProducts.map(x => x.userCategory === props.category)
    .forEach(x => selectedProductIds.push(x.id))


const change = (val, id) => {

  if (val) {
    selectedProductIds.push(id)
  } else {
    selectedProductIds.splice(selectedProductIds.indexOf(id), 1)
  }

}

</script>

<style src="../../assets/styles/buttons.css"></style>
<style scoped>

.heading{
  font-family: Open Sans;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: center;

  margin: auto;
}

.header{
  display: flex;
  padding: 10px;
}


.apply-btn {
  margin: 15px auto;
  height: 40px;
  width: 345px;

}

</style>