export default {

    map: (product) => {
        product.name = product.title
        product.addingCount = 0
        if (product.countAvailable === undefined) {
            product.countAvailable = 5
        }
        return product
    },

    mapAll: (products) => {

        const mapProduct = (product) => {
            product.name = product.title
            product.addingCount = 0

            if (product.countAvailable === undefined) {
                product.countAvailable = 5
            }

            if (product.imgUrls === undefined) {
                product.imgUrls = ["https://get.wallhere.com/photo/2000x1000-px-51-cat-funny-grumpy-humor-meme-quote-1627961.jpg"]
            }

            return product
        }

        return products.map(x => mapProduct(x))
    }

}