import httpClient from "@/httpClient/httpClient";
import httpHelper from "@/helpers/httpHelper";
import productMapper from "@/mappers/productMapper";

export default {

    GetAllProducts: async () => {
        return productMapper.mapAll(await httpClient.Get('Product/GetAll'))
    },

    GetProducts: async (filters) => {
        let query = httpHelper.ObjectToQuery(filters);

        let res = await httpClient.Get('Product/GetAllByParams?' + query)

        return productMapper.mapAll(res.products)
    },

    GetProduct: async (id) => {
        return productMapper.map((await httpClient.Get('Product/GetById?id=' + id)).createProduct)
    },

    GetFavouriteProducts: async () => {
        let res = await httpClient.Get('FavoriteItems/getFavoriteItems')

        return productMapper.mapAll(res.items)
    },

    AddToFavourite: async (id) => {
        await httpClient.Post("FavoriteItems/addItem?id=" + id, {})
    },

    RemoveFromFavourite: async (id) => {
        await httpClient.Post("FavoriteItems/removeItem?id=" + id, {})
    },

    AddToBasket: async (id) => {
        await httpClient.Post("Basket/AddItem?id=" + id, {})
    },

    GetBasketProducts: async () => {
        return  productMapper.mapAll((await httpClient.Get("Basket/GetBasketItems")).items)
    }

}